.bottle {
  position: relative;

  &__title-wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__mark {
    @extend %mark;
    left: -45px;
    top: 90px;

    &--bottle {
      left: 15px;
      top: 190px;
    }

    &::before {
      @extend %before;
    }

    @media (max-width: 800px) {
      display: none;
    }
  }

  &__title {
    @media (max-width: 800px) {
      font-size: 24px;
    }

    @media (max-width: 380px) {
      line-height: 130%;
    }

    @media (min-width: 2400px) {
      font-size: 45px;
    }
  }

  &__scketch {
    display: grid;
    justify-content: flex-end;
    grid-template-columns: 0.8fr 1.2fr;
    grid-template-rows: 1fr;
  }

  &__picture-wrap {
    display: flex;
    justify-content: start;
    width: 100%;
  }

  &__picture {
    display: flex;
    width: 102%;
    height: 100%;
    object-fit: cover;
    object-position: left center;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 51px;
    margin-top: 40px;
    margin-right: 20px;
    align-items: flex-end;
    
    @media (max-width: 1000px) {
      margin-right: -40px;
      gap: 40px;
    }

    @media (min-width: 1300px) {
      gap: 15%;
    }

    @media (min-width: 1800px) {
      gap: 18%;
    }
  }

  &__arrow {
    display: flex;
    align-items: center;
    margin-left: 10px;
  }

  &__line {
    height: 2px;
    width: 100px;
  }

  &__ellipse {
    height: 8px;
    @media (max-width: 800px) {
      display: none;
    }
  }

  &__text {
    display: flex;
    flex-direction: row;

    @media (min-width: 1700px) {
      font-size: 24px;
    }

    @media (min-width: 2400px) {
      font-size: 34px;
    }
  }

  &__text-hidden {
    @media (max-width: 1000px) {
      display: none;
    }
  }

  &__link {
    @media (max-width: 380px) {
      position: absolute;
      bottom: 0;
    }
  }

  &__button {
    @extend %learn;
    margin-top: 40px;

    @media (max-width: 800px) {
      margin-top: 30px;
    }

    @media (min-width: 2400px) {
      margin-top: 70px;
    }
  }
}
